<template>
  <div class="app fond">
            <section class="presentation">
                <img src="../../static/css/images/amphi.png" alt="Programmation sur écran de portable">
                <div class="presentation-text">
                    <h2>MMI Collection</h2>
                    <p>Vous trouverez l’intégralité des projets entrepris par nos étudiants, de 1997 à aujourd'hui. Revivez l’évolution du numérique !</p>
                </div>
            </section>

            <div class="corps-page page-projets">
                <section class="filtres ordi">
                    <h5>Filtres</h5>

                    <div class="categorie-filtres">
                        <h6>Catégorie</h6>
                        <div v-for="categorie in listeDomaines" :key="categorie.id">
                            <label :for="categorie.id">{{categorie.nom}}</label>
                            <input type="checkbox" :id="categorie.id" :name="categorie.nom" checked :value="categorie.nom">
                        </div>
                    </div>

                    <div class="categorie-filtres">
                        <h6>Date</h6>
                        <p>Entre le</p>
                        <label for="datedebut-filtre">
                            <input type="date" id="datedebut-filtre">
                        </label>
                        <p>et le</p>
                        <label for="datefin-filtre">
                            <input type="date" id="datefin-filtre">
                        </label>
                    </div>

                    <div class="categorie-filtres">
                        <h6>Niveau</h6>
                        <div>
                            <label for="DUT">DUT</label>
                            <input type="checkbox" id="DUT" checked name="DUT" value="DUT">
                        </div>
                        <div>
                            <label for="BUT">BUT</label>
                            <input type="checkbox" id="BUT" checked name="BUT" value="BUT">
                        </div>
                        <div>
                            <label for="LPW">LPW</label>
                            <input type="checkbox" id="LPW" checked name="LPW" value="LPW">
                        </div>
                        <div>
                            <label for="Tout-niveau">Tout</label>
                            <input type="checkbox" id="Tout-niveau" checked name="Tout-niveau" value="Tout-niveau">
                        </div>
                    </div>

                    <div class="categorie-filtres">
                        <h6>Type</h6>
                        <div>
                            <label for="Collectif">Collectif</label>
                            <input type="checkbox" id="Collectif" checked name="Collectif" value="Collectif">
                        </div>
                        <div>
                            <label for="Individuel">Individuel</label>
                            <input type="checkbox" id="Individuel" checked name="Individuel" value="Individuel">
                        </div>
                        <div>
                            <label for="Tout-type">Tout</label>
                            <input type="checkbox" id="Tout-type" checked name="Tout-type" value="Tout-type">
                        </div>
                    </div>
                    <div class="categorie-filtres">
                        <h6>Membre</h6>
                        <div>
                            <label for="Membre">Membre</label>
                            <input type="text" id="Membre" name="Membre" checked placeholder="Membre">
                        </div>
                    </div>
                </section>

                <section class="presentation-projets">
                    <div class="barre-info ordi">
                        <div class="tris">
                            <label for="tris">Tri :</label>
                            <select name="tris" id="tris">
                                <option value="DateCroissant">Du moins récent au plus récent</option>
                                <option value="DateDecroissant">Du plus récent au moins récent </option>
                            </select>
                        </div>
                        <p>{{listeProjets.length}} projet(s) trouvé(s)</p>
                    </div>

                    <div class="barre-info tel">
                        <svg xmlns="http://www.w3.org/2000/svg" class="logo-projet" width="27" height="18" viewBox="0 0 27 18">
                            <path class="color-objet" id="Icon_material-filter-list" data-name="Icon material-filter-list" d="M15,27h6V24H15ZM4.5,9v3h27V9ZM9,19.5H27v-3H9Z" transform="translate(-4.5 -9)"/>
                        </svg>
                        <div></div>
                        <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="sort-amount-down" class="logo-projet svg-inline--fa fa-sort-amount-down fa-w-16" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                            <path class="color-objet" d="M304 416h-64a16 16 0 0 0-16 16v32a16 16 0 0 0 16 16h64a16 16 0 0 0 16-16v-32a16 16 0 0 0-16-16zm-128-64h-48V48a16 16 0 0 0-16-16H80a16 16 0 0 0-16 16v304H16c-14.19 0-21.37 17.24-11.29 27.31l80 96a16 16 0 0 0 22.62 0l80-96C197.35 369.26 190.22 352 176 352zm256-192H240a16 16 0 0 0-16 16v32a16 16 0 0 0 16 16h192a16 16 0 0 0 16-16v-32a16 16 0 0 0-16-16zm-64 128H240a16 16 0 0 0-16 16v32a16 16 0 0 0 16 16h128a16 16 0 0 0 16-16v-32a16 16 0 0 0-16-16zM496 32H240a16 16 0 0 0-16 16v32a16 16 0 0 0 16 16h256a16 16 0 0 0 16-16V48a16 16 0 0 0-16-16z"></path>
                        </svg>
                    </div>

                    <section class="contenu">
                        <section class="choix">
                            <router-link v-for="projet in listeOrderByName" :to="{name: 'Projet', params: {id : projet.id}}" :key="projet.acf.id">
                                <div>
                                    <img :src="projet.acf.image.url" class="visuel" alt="Images projet">
                                </div>
                                <div>
                                    <img :src="projet.acf.logo.url" class="logo-projet" alt="Images projet">
                                    <h6>{{projet.acf.nom}}</h6>
                                    <i>{{projet.acf.date_de_debut}} - {{projet.acf.date_de_fin}}</i>
                                    <p>{{projet.acf.description_fr | liveSubstr(170)}}</p>
                                </div>
                            </router-link>
                        </section>
                    </section>
                </section>

            </div>
        </div>
</template>

<script>
import param from '@/param/param'

export default {
  name: 'Projets',
  data () {
    return {
      listeProjets: [],
      listeDomaines: [],
      listePromos:[],
    }
  },

  created(){
    axios.get(param.hostacf+"projet/")
    .then(response=>{
      this.listeProjets = response.data;
    })
    .catch(error => console.log(error))

    axios.get(param.host+"domaine/v1/listeDomaines")
    .then(response=>{
      this.listeDomaines = response.data;
    })
    .catch(error => console.log(error))

    axios.get(param.host+"promo/v4/listePromos")
    .then(response=>{
      this.listePromos = response.data;
    })
    .catch(error => console.log(error))
  },

    methods:{
        byCategorie: function(categorie){
            return this.listeProjets.filter(function(projet){
                return projet.domaine.id === categorie.id;
            })
        },

 
    },

  computed:{
      listeOrderByName: function(){
                function compare(a, b) {
                        if (a.nom < b.nom) return -1;
                        if (a.nom > b.nom) return 1;
                }                
                return this.listeProjets.sort(compare)
            },
  },

  filters : {
      liveSubstr: function(string, nb){
          return string.substring(0,nb) + '...';
      }
  }
}
</script>

<style scoped>

</style>




